.share{
    width: 100%;
    height: 170px;
    border-radius: 10px;
    box-shadow: 0px 0px 15px -1px rgba(0,0,0,0.68);
-webkit-box-shadow: 0px 0px 16px -8px rgba(0,0,0,0.68);
-moz-box-shadow: 0px 0px 16px -8px rgba(0,0,0,0.68);
}
.shareWrapper{
    padding: 10px;
}
.shareTop{
    display: flex;
    align-items: center;
}
.shareInput{
    border: none;
    width: 80%;
}
.shareInput:focus{
    outline: none;
}
.shareHr{
    margin: 20px;
    
}
.profileImage{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.shareBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.shareOptions{
    display: flex;
    margin-left: 10px;
}
.shareOption{
    display: flex;
    align-items: center;
    margin-right: 10px;
    cursor: pointer;
}
.shareIcon{
    margin-right: 3px;
    font-size: 18px;
}
.shareButton{
    border: none;
    padding: 7px;
    border-radius: 10px;
    margin-right: 20px;
    cursor: pointer;
    background-color: rgb(12, 170, 12);
}